body{
    margin:0;
    font-family:sans-serif;
    background-color: #dddd;
}
.todo-container{
    width:300px;
    text-align: center;
    margin:0 auto;
    padding-top: 50px;
}
.todo-container form{
    padding:15px;
    background-color: #ececec;
    border-radius: 10px;
}
.todo-header{
    color: #00abcd;
}
.input-section{
background-color: #fff;
border-radius: 10px;
outline-color: #dddd;
}
.input-section input{
    border: none;
    box-sizing: border-box;
    width: 100%;
    border-radius: 10px;
    padding: 10px;
    outline: none;
    color: #00abcd;
}
.input-section input::placeholder{
    color: #00abcd;
}
ul{

    min-height: 200px;
    padding: 0px;
    text-align: left;
    border-radius: 10px;
}
ul li {
    padding: 10px;
    list-style: none;
    text-align: left;
    background-color: #ececec;
    margin-bottom: 10px;
    border-radius: 10px;
    color: #00abcd;
    display: flex;
}
ul li span{
    flex:1;
    text-align: right;
    margin-left: 10px;
}
ul li span i{
    cursor: pointer;
}
.edit-icon{
    margin:0 10px
}
